<template>
    <div class="search">
        <input type="text" :placeholder="placeholder" v-model="msg" @keydown.enter="showSearch">
    </div>
</template>
<style scoped lang="scss">
.search {
    background-color: #fff;
    padding: 10px 20px;
    display: flex;
    input {
        flex: 1;
        border: none;
        outline: none;
        background: #efefef;
        font-size: 12px;
        padding: 10px 20px;
        border-radius: 17px;
    }
}
</style>
<script>
export default {
    data() {
        return {
            msg: ''
        }
    },
    props: ['placeholder', 'search'],
    methods: {
        showSearch() {
            this.search(this.msg);
            this.msg = '';
        }
    }
}
</script>