<template>
    <div class="page-list">
        <Search placeholder="请输入要搜索的内容" :search="value => $store.commit('search', value)"></Search>
        <!-- <h1>{{$store.state.search}}</h1> -->
        <ul class="orders">
            <li v-for="item in orders" :key="item.id" @touchend="orderList(item.id)">{{item.text}}</li>
        </ul>
        <Product v-for="item in list" :key="item._id" :data="item"></Product>
        <div v-show="others.length" class="showMore" @touchend="showMore">查看更多商品</div>
    </div>
</template>
<style scoped lang="scss">
@import '@/common.scss';

.page-list {
    .orders {
        background: #fff;
        font-size: 14px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        display: flex;
        border-bottom: 1px solid #ccc;
        padding: 0;

        li {
            flex: 1;
            position: relative;
            border-right: 1px solid #ccc;

            &:last-child {
                border-right: none;
            }

            &::before,
            &::after {
                content: '';
                position: absolute;
                @include arrow(4px);
                top: 14px;
                left: 80px;
            }

            &::after {
                border-top-color: #fff;
                top: 13px;
            }
        }
    }
    .showMore {
        background-color: #fff;
        border-bottom: 1px solid #ccc;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: $color;
        text-indent: -1em;
        span {
            position: relative;
            &::before,
            &::after {
                @include arrow(7px, $color);
                content: "";
                position: absolute;
                top: 10px;
                right: -18px;
            }
            &::after {
                top: 8px;
                border-top-color: #fff;
            }
        }
    }

}
</style>
<script>
import Search from '@/components/Search';
import Product from '@/components/Product';
export default {
    components: { Search, Product },
    data() {
        return {
            //排序列表
            orders: [
                { text: '价格排序', id: 'price' },
                { text: '销量排序', id: 'sales' },
                { text: '好评排序', id: 'evaluate' },
                { text: '优惠排序', id: 'discount' }
            ],
            // 商品列表
            list: [],
            //隐藏的列表
            others: []
        }
    },
    computed: {
        // 代理state数据
        dealSearch() {
            return this.$store.state.search;
        }
    },
    methods: {
        //显示更多
        showMore() {
            setTimeout(() => { this.list = this.list.concat(this.others);this.others = []; }, 300)
        },
        //数据排序
        orderList(id) {
            // if (id == 'discount') {

            //     this.list.sort((a, b) => {
            //         return (a.originPrice - a.price) - (b.originPrice - b.price)
            //     })
            // }else{
            //     this.list.sort((a, b) => {
            //         return a[id] - b[id];
            //     })
            // }

            this.$http.get('/data/order', { params: { type: this.$route.params.id, id, mode: -1 } })
                .then(({ data }) => {
                    if (!data.length) {
                        alert('没有数据')
                    }
                    this.list = data.slice(0, 3);
                    this.others = data.slice(3);
                })
        },
        //请求数据
        getData() {
            // console.log(this.$route);
            let { params } = this.$route;
            // console.log(params)
            this.$http.get('/data/list', { params })
                .then(({ data }) => {
                    // console.log(data);
                    this.list = data.slice(0, 3);
                    this.others = data.slice(3);
                })
        }
    },
    created() {
        this.getData();
    },
    watch: {
        $route() {
            this.getData();
        },
        dealSearch(value){
            this.$http.get('/data/search', { params: {type: this.$route.params.id, value}})
            .then(({data}) => {
                if (!data.length) {
                    alert('没有返回数据')
                }else{
                    this.list = data.slice(0, 3);
                    this.others = data.slice(3);
                }
            })
        }
    }
}
</script>